@import "../../inc/vars";
.acrobits_call-to-action {
	text-align: center;
	position: relative;
	z-index: 3;
	.container {
		padding-top: 48px;
		padding-bottom: 48px;
		@media(min-width: $screen-sm) {
			padding-top: 80px;
			padding-bottom: 80px;
		}
	}
	&-title {
		@include pop(400, 35, 1.24);
		background: var(--grad-title, linear-gradient(180deg, #2D3442 0%, #6C7689 100%));
		background-clip: text;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		margin-bottom: 16px;
		@media(min-width: $screen-sm) {
			margin-bottom: 32px;
		}
	}
	&-subtitle {
		@include pop(400, 18, 1.24);
		margin-bottom: 16px;
		color: #5A6272;
		@media(min-width: $screen-sm) {
			@include pop(400, 24, 1.24);
			margin-bottom: 8px;
		}
	}
	&-button {
		display: inline-block;
		color: #fff;
		text-decoration: none;
		padding: 24px 16px;
		min-width: 240px;
		width: 100%;
		border-radius: 6px;
		background: var(--Secondary-400, $green);
		@include pop(500, 16, 1);
		letter-spacing: 0.16px;
		transition: .3s;
		box-shadow: 0px 1px 3px 0px rgba(39, 168, 125, 0.40), 0px 4px 8px 0px rgba(39, 168, 125, 0.20), 0px 6px 20px 0px rgba(39, 168, 125, 0.15);
		&:hover {
			background: #278763;
		}
		@media(min-width: $screen-sm) {
			width: auto;
		}
	}
	img {
		max-width: 100%;
		height: auto;
		display: inline-block;
		vertical-align: top;
	}
	&_style_1 {
		overflow: hidden;
		img {
			position: relative;
			width: 934px;
			max-width: calc(100% - 40px);
			bottom: -20px;
			border-radius: 20px;
			filter: drop-shadow(2px 4px 20px rgba(0,0,0,0.1));
			@media (min-width: $screen-md) {
				height: 170px;
				object-fit: cover;
				object-position: top center;
			}
		}
	}
	&_style_2 {
		.container {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			margin-bottom: 0;
			h2 {
				order: -1;
				margin-bottom: 24px;
				@media(min-width: $screen-sm) {
					font-size: 53px;
					line-height: 124%;
					letter-spacing: -1.06px;
					margin-bottom: 16px;
				}
			}
			.acrobits_call-to-action-subtitle {
				margin-bottom: 40px;
				@include fig(400, 20, 1.56);
			}
			.button {
				width: 100%;
				@media(min-width: $screen-sm) {
					width: 240px;
				}
			}
		}
	}
	&_style_3 {
		padding: 60px 20px;
		overflow: hidden;
		@media(min-width: $screen-sm) {
			margin: 0 40px;
			width: auto;
			background: #FBF6FF;
		}
		img {
			position: relative;
			transform: scale(1.5);
			@media(min-width: $screen-md) {
				margin-top: -60px;
				transform: none;
			}
		}
		.container {
			padding: 0;
			display: flex;
			flex-direction: column;
			align-items: center;
			position: relative;
			&:before {
				content: '';
				display: block;
				width: 150%;
				height: 0;
				padding-bottom: 75%;
				transform: translateX(-50%);
				position: absolute;
				left: 50%;
				top: 100%;
				border-radius: 100%;
				opacity: 0.3;
				background: #532D9F;
				filter: blur(120px);
				@media(min-width: $screen-sm) {
					width: 70%;
				}
			}
			h2 {
				font-size: 34px;
				line-height: 1.24;
				margin-bottom: 16px;
				@media(min-width: $screen-sm) {
					font-size: 44px;
					line-height: 1.12;
				}
			}
			.acrobits_call-to-action-subtitle {
				order: 2;
				@include fig(400, 20, 1.24);
				margin-bottom: 22px;
				@media(min-width: $screen-sm) {
					font-size: 23px;
					margin-bottom: 31px;
					padding: 0 30px;
				}
			}
			.button {
				order: 3;
				width: auto;
				position: relative;
				z-index: 2;
				margin-bottom: 60px;
				@media(min-width: $screen-sm) {
					margin-bottom: 0;
				}
			}
		}
	}
	&_style_4 {
		padding: 0 20px;
		width: 1400px;
		max-width: 100%;
		margin: 0 auto;
		.container {
			padding: 0;
			display: flex;
			flex-direction: column;
			align-items: center;
			position: relative;
			.acrobits_call-to-action-subtitle {
				color: #5A6272;
				@include pop(400, 20, 1.36);
				margin-bottom: 32px;
				@media (min-width: $screen-sm){
					font-size: 24px;
					margin-bottom: 23px;
				}
			}
			.acrobits_call-to-action-title {
				@include pop(400, 30, 1.24);
				margin-bottom: 36px;
				@media (min-width: $screen-sm){
					font-size: 36px;
					margin-bottom: 46px;
				}
			}
			.acrobits_call-to-action-button {
				min-width: 170px;
				height: 42px;
				margin-bottom: 46px;
				display: flex;
				align-items: center;
				justify-content: center;
				@media (min-width: $screen-sm){
					height: 64px;
					margin-bottom: 100px;
				}
			}
		}
		img {
			width: 972px;
			margin-right: -10%;
		}
	}
	&_style_5 {
		padding: 0 20px;
		width: 1400px;
		max-width: 100%;
		margin: 0 auto;
		.container {
			background: #fff;
			padding: 39px 35px;
			display: flex;
			flex-direction: column;
			align-items: center;
			box-shadow: 0px 2px 4px 0px rgba(7, 4, 146, 0.10), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 12px 24px 0px rgba(27, 59, 119, 0.05);
			@media (min-width: $screen-sm){
				padding: 80px 100px;
			}
			.acrobits_call-to-action-subtitle {
				font-size: 20px;
				margin-bottom: 24px;
				@media (min-width: $screen-sm){
					font-size: 24px;
					margin-bottom: 18px;
				}
			}
			.acrobits_call-to-action-title {
				font-size: 30px;
				margin-bottom: 30px;
				@media (min-width: $screen-sm){
					font-size: 36px;
				}
			}
			.acrobits_call-to-action__description {
				@include fig(400, 20, 1.56);
				margin-bottom: 39px;
				color: #4F4B58;
				@media (min-width: $screen-sm){
					font-size: 24px;
					margin-bottom: 47px;
				}
			}
		}
	}
}